export default {
	props: [
			'tabData'
	],
	data() {
		return {



			

				boxTop: 0,

			
			tabData2: [
				{	//一个tab选项
				
					text: '分类',
					seleIndex: 0,	//当前选中的下拉选项
					dropData: []
				}
			],
			
			tabIndex: 0,
			
			dropOpen: false,
			
			//是否有移动
			maskMove: false,
			
			timeID: ''
		}
	},
	methods: {
		//tab被点击时
		tabChange: function(index){
			
			if(index==this.tabIndex) {
				//下拉展开取反
				this.dropOpen = !this.dropOpen
			}
			else {
				this.tabIndex = index
				this.dropOpen = false
			}
		}
		
		//下拉关闭
		,dropOff: function(){
			this.dropOpen = false
		},
		
		//蒙版触摸转换成点击
		maskstart: function(){
		},
		maskmove: function(){
			this.maskMove=true;
		},
		maskend: function(){
			let that = this;
				if(!this.maskMove) {
					this.dropOff()
				}	
			
			this.maskMove=false;
		},
		
		//下拉内容选择改变时
		seleChange: function(tabIndex, index){
			this.tabData2[tabIndex].seleIndex = Number(index);
			this.tabData2[tabIndex].text = this.tabData2[tabIndex].dropData[index].title;
			this.dropOpen = false
		}
	},
	
	//渲染完成后
	mounted: function(){
		this.tabData2 = this.tabData
	},
	watch: {
	　　tabData:{
			handler: function(newVal,oldVal){
				this.tabData2 = this.tabData
			},	
	　　　　deep:true
	　　}
	}
}
